        line-height: 25px;
        text-align: center;
        letter-spacing: 0.01em;
        color: #525252; }
    p {
        font-family: Gothic;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 25px;
        text-align: center;
        letter-spacing: 0.01em;
        color: #525252; } }
.purpose {
    font-size: 14px!important;
    line-height: 17px!important;
    padding: 0!important; }
