@include font-face('Museo0', '../fonts/MuseoSansCyrl-300', normal, 300);
@include font-face('Museo1', '../fonts/MuseoSansCyrl-700', normal, 700);
@include font-face('Museo2', '../fonts/MuseoSansCyrl-900', normal, 900);
/*! Fonts End */
