    // display: flex
    // flex-direction: column
    // justify-content: flex-end
    overflow: hidden; }

.animation-form__animation {
    position: relative;
    width: 100%;
    height: 100% {
        //transition-duration: 5s
 } }        //transition-timing-function: cubic-bezier(0, 0, 1, 1)

.animation-one {
    top: 100px;
    position: absolute;
    animation: moveFirst 30s infinite linear;
    z-index: 1; }

.animation-two {
    bottom: 80px;
    position: absolute;
    animation: moveSecond  25s infinite linear;
    z-index: 1; }

@keyframes moveFirst {
  0% {
    right: -544px; }
  50% {
    right: -200px; }

  100% {
    right: 0px; } }

@keyframes moveSecond {
  0% {
    right: -544px; }
  50% {
    right: -200px; }
  100% {
    right: 0px; } }




