/*! .header */
.header, .bg-blue {
  height: 106px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  transition: all linear .5s;
  transition: background 1s;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 23px;
  letter-spacing: 0.02em;
  font-family: Museo;
  z-index: 9999;
}

.header {
  width: 100%;
  height: 106px;
  z-index: 9999;
  background-color: rgba(255, 255, 255, 0.3);
}

.bg-blue {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)), rgba(255, 255, 255, 0.3);
  display: none;
  z-index: 9999;
}

.main-color-change-dark {
  transition: all linear 1s;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)), rgba(255, 255, 255, 0.3) !important;
}

.main-color-change {
  transition: all linear 1s;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), rgba(255, 255, 255, 0.3) !important;
}

.burger-menu {
  position: absolute;
  right: 40px;
  width: 34px;
  height: 50px;
  cursor: pointer;
}

.burger {
  position: absolute;
  background: #fff;
  width: 100%;
  height: 5px;
  top: 50%;
  border-radius: 8px;
  right: 0px;
  margin-top: -5px;
  opacity: 1;
}

.burger::before {
  border-radius: 8px;
  position: absolute;
  background: #fff;
  width: 34px;
  height: 5px;
  top: 10px;
  content: "";
  display: block;
}

.burger::after {
  border-radius: 8px;
  position: absolute;
  background: #fff;
  width: 34px;
  height: 5px;
  bottom: 10px;
  content: "";
  display: block;
}

.burger::after::before, .burger::after .burger, .burger::before, .burger .burger {
