/*! .header */
.header, .bg-blue {
  height: 106px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  transition: all linear .5s;
  transition: background 1s;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 23px;
  letter-spacing: 0.02em;
  font-family: Museo;
  z-index: 9999; }
.header {
  width: 100%;
  height: 106px;
  z-index: 9999;
  background-color: rgba(255, 255, 255, 0.3);
 }  //z-index: -4


.bg-blue {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)), rgba(255, 255, 255, 0.3);
  display: none;
  z-index: 9999; }

.main-color-change-dark {
  transition: all linear 1s;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)), rgba(255, 255, 255, 0.3)!important; }

.main-color-change {
  transition: all linear 1s;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), rgba(255, 255, 255, 0.3)!important; }

.burger-menu {
  position: absolute;
  right: 40px;
  width: 34px;
  height: 50px;
  cursor: pointer; }

.burger {
  position: absolute;
  background: #fff;
  width: 100%;
  height: 5px;
  top: 50%;
  border-radius: 8px;
  right: 0px;
  margin-top: -5px;
  opacity: 1; }

.burger {
  &::before {
    border-radius: 8px;
    position: absolute;
    background: #fff;
    width: 34px;
    height: 5px;
    top: 10px;
    content: "";
    display: block; } }

.burger {
  &::after {
    border-radius: 8px;
    position: absolute;
    background: #fff;
    width: 34px;
    height: 5px;
    bottom: 10px;
    content: "";
    display: block; } }

.burger::after, .burger {
  &::before, .burger {
    transition: all .3s ease-in-out;
    -webkit-transition: all .3s ease-in-out; } }

.burger-menu.menu-on .burger {
  &::after {
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    bottom: 0px; } }

.burger-menu.menu-on .burger {
  &::before {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    top: 0px; } }

.burger-menu.menu-on .burger {
 background: rgba(111,111,111,.0); }




.header__container {
  margin: 0 106px;
  transition: all linear .5s; }


.header__content {
  transition: all linear .5s;
  display: flex;
  align-items: center;

  a {
    text-decoration: none; } }

.header__content-logo {
  display: flex;
  align-items: center;
  width: 45%;
  transition: all linear .5s;
  p {
    padding: 0 20px;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 23px;
    letter-spacing: 0.02em; }
  img {
    padding-top: 5px; } }

.header__content-phones {
  ul {
    display: flex;
    li {
      a {
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 25px;
        letter-spacing: 0.02em;
        color: $white;
        text-decoration: none; } } } }

.header__content-callback {
  display: flex;
  align-items: center; }

.hidden-links_block {
  transition: all linear .5s;
  background-image: url('../img/ract.svg');
  background-size: cover;
  width: 235px;
  height: 79px;
  display: none;
  opacity: 1;
  flex-direction: column;
  position: absolute;
  padding: 20px;
  color: $dark;
  li {
    padding: 3px!important;
    a {
      font-family: Museo!important;
      transition: all linear .5s;
      padding: 5px!important;
      font-style: normal!important;
      font-weight: normal!important;
      font-size: 14px!important;
      line-height: 20px!important;
      letter-spacing: 0.02em!important;
      color: #000000!important;
      text-decoration: none!important;
      &:hover {
        color: $accentcolor!important; } } } }
.hidden-links {
  &:hover ~ .hidden-links_block {
    a {
      margin-left: 0; } } }

