.bottom-line {
  border-bottom: 1px solid $white;
  transition: all linear .5s;
  margin: 0 27px;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 0.02em;
  &:hover {
    border-bottom: 1px solid $white; } }

.header__content-callback {
  border-radius: 100px;
  background: rgba(255, 255, 255, 0.1);
  padding: 0 10px;
  img {
    z-index: 999; }
  p {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    letter-spacing: 0.02em;
    padding: 15px 12px;
    color: $white; }
  &:hover {
    img {
      animation-duration: 1.5s;
      animation-name: slidein; } } }

@keyframes slidein {
  0% {
    transform: rotate(0); }

  50% {
    transform: rotate(45deg); }

  100% {
    transform: rotate(0); } }

.content-main {
  display: flex;
  justify-content: space-between;
  margin-left: 116px; }
.header-content-menu {
  position: relative;
  ul {
    li {
      padding: 9px 0;
      a {
        transition: all linear .5s;
        font-family: Gothic1;
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 22px;
        letter-spacing: 0.01em;
        text-transform: uppercase;
        color: $white; }
      &:hover {
        a {
          text-decoration: none;
          margin-left: 10px; } } } } }

.header-content-mobile {
  width: 100%;
  height: auto;
  position: fixed;
  padding: 20px;
  left: 0;
  right: 0;
  top: 106px;
  z-index: 9999;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)), rgba(255, 255, 255, 0.3);
  font-size: 14px!important;
  line-height: 17px!important;
  letter-spacing: 0.01em!important; }

.header-content {
  width: 100%;
  height: auto;
  position: fixed;
  padding: 20px;
  left: 0;
  right: 0;
  top: 106px;
  z-index: 9999;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)), rgba(255, 255, 255, 0.3); }
.header-content-img {
  position: absolute;
  right: 40px;
  top: 100px; }

